    p.faqText You can ask your questions in the private
      a.faqLink(href="#" target="_blank")  Facebook group
      | . When posting your question please specify the module, the chapter, the task number and the task statement.
    p.faqSubtitle Other questions
    p.faqText Contact us by 
      a.faqLink(href="mailto:support@gmail.ru" target="_blank")  support@gmail.ru
