<script>
import Modal from '@/components/ui/Modal';

export default {
  name: 'FAQ',
  components: {
    Modal,
  },
  props: {
    value: Boolean,
  },
  computed: {
    isOpened: {
      set: function(value) {
        this.$emit('input', value);
      },
      get: function() {
        return this.value;
      },
    },
  },
};
</script>

