<template>
  <div class="rating">
    <a
      :class="{ star: true, disable: disabled }"
      href="#"
      v-for="star in starList"
      :key="star.value"
      @mouseenter="onMouseEnter(star)"
      @mouseleave="onMouseLeave(star)"
      @click.prevent="onClick(star)"
    >
      <Star v-bind="star" />
    </a>
  </div>
</template>

<script>
import Star from '@/components/icons/Star';

export default {
  name: 'Rating',
  components: { Star },
  props: {
    value: {
      type: Number,
      default: 0,
    },
    max: {
      type: Number,
      default: 5,
    },
    color: {
      type: String,
      default: 'primary',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data: () => ({
    hoveringStar: null,
  }),
  computed: {
    starList() {
      return Array.apply(null, Array(this.max)).map((item, index) => {
        if (this.hoveringStar) {
          return index + 1 > this.hoveringStar
            ? { value: index + 1, silver: true }
            : { value: index + 1, [this.color]: true };
        } else {
          return index + 1 > this.value ? { value: index + 1, silver: true } : { value: index + 1, [this.color]: true };
        }
      });
    },
  },
  methods: {
    onMouseEnter(star) {
      if (this.disabled) return;

      this.hoveringStar = star.value;
    },
    onMouseLeave() {
      if (this.disabled) return;

      this.hoveringStar = null;
    },
    onClick(star) {
      if (this.disabled) return;
      this.$emit('input', star.value);
    },
  },
};
</script>

<style lang="scss" src="./Rating.scss" scoped />
